.stores {
  margin: 0.6rem 0;
  .store-img {
    height: 218px;
    width: 100%;
    object-fit: cover;
    border-radius: $border-radius-large;
    border: 1px solid #c4c4c4;
    @media (max-width: 768px) {
      height: 150px;
    }
  }

  .store-name {
    font-weight: 700;
    margin: 0.6rem 0 0.2rem;
    @media (max-width: 768px) {
      font-size: 0.72rem;
    }
  }
  .store-address {
    p {
      font-size: 0.75rem;
        width: 70%;
    }
    span {
      width: 30%;
      text-align: right;
       font-size: 0.7rem;
    }
    @media (max-width: 768px) {
      font-size: 0.5rem;
    }
  }
  a {
    color: $medium-gray;
  }
  a:hover {
    text-decoration: none;
  }
  @media (max-width: 768px) {
    p,
    span {
      font-size: 0.5rem !important;
    }
  }
}

.product-listing {
  margin: 0.6rem 0;
  .product-block {
    background-color: #f6f6f6;
    padding: 40px 12px;
    position: relative;
    border-radius: $border-radius-large;
    margin-bottom: 8px;
    .product-location {
      position: absolute;
      font-size: 12px;
      top: 18px;
      @media (max-width: 768px) {
        font-size: 0.7rem;
      }
    }
    .product-img {
      height: 160px;
      width: 160px;
      margin: 8px auto 0;
      display: flex;
      object-fit: contain;
      @media (max-width: 768px) {
        height: 130px;
      }
    }
  }

  .product-name {
    font-size: $font-size-small;
    margin: 0.6rem 0 0.2rem;
    color: $medium-gray;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    @media (max-width: 768px) {
      font-size: 0.7rem;
    }
  }
  a {
    color: $medium-gray;
  }
  a:hover {
    text-decoration: none;
  }

  .product-price {
    color: $dark-gray;
    font-weight: 600;
    margin: 0;
    line-height: 1;
    .miniprice {
      font-size: 0.9rem;
      text-decoration: line-through rgba($color: #000000, $alpha: 0.46) 2px
        solid;
      color: rgba($color: #000000, $alpha: 0.46);
      @media (max-width: 768px) {
        font-size: 0.7rem;
      }
    }
    @media (max-width: 768px) {
      font-size: 0.85rem;
    }
  }
}

.product-filter {
  display: flex;
  align-items: flex-start;
  margin-bottom: 14px;
  .dropdown-toggle {
    margin-right: 24px;

  }
  .btn {
    color: #666666;
    font-size: 14px;
     margin: 0 12px 6px 0;
  }
  .dropdown-menu:before {
    color: #fbd128;
  }
  .dropdown .dropdown-menu,
  .btn-warning {
    background-color: #fbd128;
  }
 
}

.miniprice {
  font-size: 1.2rem;
  text-decoration: line-through rgba($color: #000000, $alpha: 0.26) 2px solid;
  color: rgba($color: #000000, $alpha: 0.26);
}
.cart-block {
  max-height: 404px;
  overflow-y: auto;
  padding-bottom: 8px;
}
.cart-item-block {
  display: flex;
  align-items: center;
  border: 1px solid #eaeaea;
  border-radius: 5px;
  margin-bottom: 8px;
  padding: 8px 12px;

  p {
    margin: 0;
  }
  .action-buttons {
    .btn {
      color: #333333;
    }
  }
  .product-img {
    height: 92px;
    width: 88px;
    margin-right: 8px;
    display: flex;
    object-fit: contain;
  }
  .product-name {
    font-size: $font-size-small;
    margin-bottom: 0.2rem;
    color: #999999;
    padding-right: 14px;
  }
  .product-location {
    font-size: 12px;
    color: #999999;
    @media (max-width: 768px) {
      font-size: 0.7rem;
    }
  }
  .product-price {
    color: $dark-gray;
    font-weight: 600;
    margin: 0;
  }
  .product-amount {
    padding: 0 12px;
    font-weight: 600;
  }
}
