.footer {
  padding: 24px 0;

  &.footer-default {
    background-color: #d5f6e1;
  }

  nav {
    display: inline-block;
    float: left;
  }

  .social-grid {
    display: flex;

    li {
      a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        border-radius: 18px;
        border: 1px solid;
        transition: all 0.2s ease-in;
         @media (max-width: 768px) {
              width: 24px;
        height: 24px;
         }

        &:hover {
          color: $primary-color;
          border-color: $primary-color;
          text-decoration: none;
        }
      }
    }
  }

  ul {
    margin-bottom: 0;
    padding: 0;
    list-style: none;

    li {
      display: block;
      font-size: $font-size-base;
      padding: $padding-base-vertical;
      font-weight: 600;
      color: $dark-gray;
       @media (max-width: 768px) {
         padding: 0.15rem;
         font-size: 14px;
       }

      a {
        color: $medium-gray;
        text-transform: none;
        text-decoration: none;
        font-weight: 400;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .copyright {
    font-size: $font-size-small;
  }

  &:after {
    display: table;
    clear: both;
    content: " ";
  }

  @media (max-width: 768px) {
    a {
      text-align: center;
      .entermarket-logo {
        width: 70%;
        margin-bottom: 1rem;
      }
    }
    a .app-image {
      width: 80%;
      text-align: center;
    }
  }
}
